html,
body {
  height: 100%; }

* {
  box-sizing: border-box; }

*:before,
*:after {
  box-sizing: border-box; }

html {
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  scrollbar-color: rgba(255, 255, 255, 0.3) rgba(46, 43, 57, 0.3);
  overflow-x: hidden; }
  html::-webkit-scrollbar {
    width: 10px; }
  html::-webkit-scrollbar-thumb {
    background-color: rgba(46, 43, 57, 0.3);
    border-radius: 0; }

.body {
  width: 980px;
  height: 630px;
  margin-left: auto;
  margin-right: auto;
  color: #000;
  font-size: 16px;
  text-rendering: optimizeSpeed;
  font-family: Montserrat, Helvetica, sans-serif;
  background-image: url(../images/back.png);
  background-repeat: no-repeat;
  background-size: cover; }

*:focus,
button:focus {
  outline: none; }

button:focus {
  box-shadow: none !important; }

button {
  transition: color 0.3s, border-color 0.3s, transform 0.3s, background-color 0.3s !important; }

svg {
  transition: fill 0.3s, filter 0.3s, stroke 0.3s; }

a {
  position: relative;
  color: #000;
  transition: color 0.3s, background-color 0.3s, border-color 0.3s !important; }
  a:hover {
    text-decoration: none;
    color: #CC2128; }
  a:focus {
    text-decoration: none;
    color: #000;
    box-shadow: none !important; }

a:not([class]) {
  text-decoration-skip-ink: auto; }

.break:after {
  content: '\A';
  white-space: pre; }

input,
button,
textarea,
select {
  font: inherit; }

input:focus::placeholder,
textarea:focus::placeholder {
  color: transparent; }

img,
video,
audio {
  max-width: 100%;
  height: auto; }

sub,
sup {
  line-height: 0; }

label,
button,
select {
  cursor: pointer; }

input {
  cursor: text; }

[disabled] {
  cursor: default; }

abbr[title],
acronym[title],
dfn {
  cursor: help; }

textarea {
  resize: vertical; }

input,
textarea {
  background-clip: padding-box; }

p:last-child {
  margin-bottom: 0; }

.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.mobile {
  display: none !important; }
  @media (max-width: 767px) {
    .mobile {
      display: inline-block !important; } }

@media (max-width: 767px) {
  .desktop {
    display: none !important; } }

.center {
  text-align: center; }

.content {
  flex: 1 0 auto; }

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.friends {
  flex-grow: 1;
  height: 63px;
  background-image: url(../images/friends.png);
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  padding: 0 10px; }
  .friends__slider {
    flex-grow: 1;
    height: 63px;
    position: relative;
    overflow: hidden; }
  .friends__items {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 51px;
    display: flex;
    align-items: center;
    transition: left 0.3s;
    will-change: left; }
  .friends__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 51px;
    min-width: 50px;
    margin-right: 10.5px;
    background-image: url(../images/friend-bg.png);
    background-repeat: no-repeat; }
  .friends__arrow:disabled {
    opacity: 0.5; }
  .friends__arrow_left {
    margin-right: 5px; }
  .friends__add {
    position: absolute;
    top: -9px;
    right: -7px; }

.girl {
  position: absolute;
  top: 71%;
  left: 44%;
  width: 23px;
  height: 67px;
  background-image: url(../images/girl.png);
  background-repeat: no-repeat;
  transition: left 0.3s, top 0.3s;
  will-change: left, top;
  animation-fill-mode: forwards;
  animation-duration: 2s; }
  .girl.first {
    animation-name: first; }
  .girl.second {
    animation-name: second; }
  .girl.third {
    animation-name: third; }
  .girl.fourth {
    animation-name: fourth; }
  .girl.five {
    animation-name: five; }
  .girl.six {
    animation-name: six; }
  .girl.seven {
    animation-name: seven; }
  .girl.eight {
    animation-name: eight; }
  .girl.nine {
    animation-name: nine; }
  .girl.ten {
    animation-name: ten; }

@keyframes first {
  0% {
    top: 71%;
    left: 44%; }
  50% {
    top: 65%;
    left: 38%; }
  100% {
    top: 67%;
    left: 35%; } }

@keyframes second {
  from {
    top: 67%;
    left: 35%; }
  to {
    top: 74%;
    left: 27%; } }

@keyframes third {
  from {
    top: 74%;
    left: 27%; }
  to {
    top: 77%;
    left: 19%; } }

@keyframes fourth {
  from {
    top: 77%;
    left: 19%; }
  to {
    top: 72%;
    left: 10%; } }

@keyframes five {
  0% {
    top: 72%;
    left: 10%; }
  50% {
    top: 67%;
    left: 8%; }
  100% {
    top: 62%;
    left: 12%; } }

@keyframes six {
  0% {
    top: 62%;
    left: 12%; }
  50% {
    top: 58%;
    left: 11%; }
  100% {
    top: 54%;
    left: 14%; } }

@keyframes seven {
  from {
    top: 54%;
    left: 14%; }
  to {
    top: 48%;
    left: 21%; } }

@keyframes eight {
  0% {
    top: 48%;
    left: 21%; }
  50% {
    top: 44%;
    left: 23%; }
  100% {
    top: 37%;
    left: 17%; } }

@keyframes nine {
  0% {
    top: 37%;
    left: 17%; }
  50% {
    top: 31%;
    left: 11%; }
  100% {
    top: 28%;
    left: 13%; } }

@keyframes ten {
  0% {
    top: 28%;
    left: 13%; }
  25% {
    top: 25%;
    left: 15%; }
  50% {
    top: 22%;
    left: 14%; }
  75% {
    top: 20%;
    left: 17%; }
  100% {
    top: 23%;
    left: 20%; } }

.main {
  position: relative;
  height: 100%;
  background-image: url(../images/all.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  overflow: hidden; }

.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
  opacity: 0;
  z-index: 10;
  transition: opacity 0.3s; }
  .modal.show {
    opacity: 1;
    pointer-events: auto; }
    .modal.show .modal__content {
      transform: translateY(87.5px); }
  .modal__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.52); }
  .modal__content {
    position: absolute;
    left: calc(50% - (523px / 2));
    z-index: 11;
    width: 523px;
    height: 455px;
    background-image: url(../images/modal-content.png);
    background-repeat: no-repeat;
    transition: transform 1s;
    will-change: top; }
  .modal__close {
    position: absolute;
    top: 15px;
    right: 15px; }
  .modal__title {
    height: 33px;
    margin-bottom: 60px;
    background-image: url(../images/modal-title.png);
    background-repeat: no-repeat;
    text-align: center;
    background-position: center;
    transform: translateY(-14px);
    color: #fff;
    font-size: 27px;
    line-height: 33px; }

.navigation {
  margin-right: 35px; }
  .navigation__items {
    display: flex;
    align-items: center; }
  .navigation__button {
    position: relative; }
    .navigation__button:hover .navigation__normal {
      opacity: 0; }
    .navigation__button:hover .navigation__hover {
      opacity: 1; }
  .navigation__image {
    transition: opacity 0.3s; }
  .navigation__hover {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0; }

.reset-btn {
  border: none;
  background-color: transparent;
  padding: 0; }

.table {
